
"use client";

import { useState } from "react";
import { useRouter } from "next/router";
import Navbar from "../components/Navbar";
import axios from "axios";
import styles from '../../public/StyleSheet/Trains.module.';

export default function Trains() {
  const [trains, setTrains] = useState([]);
  const [fromStationCode, setFromStationCode] = useState("");
  const [toStationCode, setToStationCode] = useState("");
  const [dateOfJourney, setDateOfJourney] = useState("");
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const stationList = [
    { code: "BVI", name: "Borivali" },
    { code: "NDLS", name: "New Delhi" },
    { code: "MMCT", name: "Mumbai Central" },
    { code: "CSTM", name: "Chhatrapati Shivaji Maharaj Terminus" },
    { code: "BDTS", name: "Bandra Terminus" },
    { code: "MS", name: "Chennai Egmore" },
    { code: "NZM", name: "Hazrat Nizamuddin" },
    { code: "SBC", name: "Bengaluru City" },
    { code: "JBP", name: "Jabalpur" },
    { code: "KOAA", name: "Kolkata" },
    { code: "PNQ", name: "Pune" },
    { code: "TATA", name: "Tatanagar" },
    { code: "DR", name: "Dadar" },
    { code: "PAT", name: "Patna" },
    { code: "MDU", name: "Madurai" },
    { code: "AGC", name: "Agra Cantt" },
    { code: "CNB", name: "Kanpur" },
    { code: "LU", name: "Lucknow" },
    { code: "JAL", name: "Jalandhar" },
    { code: "VSKP", name: "Visakhapatnam" },
    { code: "BBS", name: "Bhubaneswar" },
    { code: "CBE", name: "Coimbatore" },
    { code: "MYS", name: "Mysuru" },
    { code: "KGP", name: "Kharagpur" },
    { code: "UDZ", name: "Udaipur" },
    { code: "PURI", name: "Puri" },
    { code: "HYD", name: "Hyderabad" },
    { code: "BPL", name: "Bhopal" },
    { code: "RNC", name: "Ranchi" }
  ];

  const fetchTrains = async () => {
    setLoading(true);
    try {
      const response = await axios.get("https://irctc1.p.rapidapi.com/api/v3/trainBetweenStations", {
        params: { fromStationCode, toStationCode, dateOfJourney },
        headers: {
          "x-rapidapi-key": `${process.env.NEXT_PUBLIC_RAPIDAPI_KEY}`,
          "x-rapidapi-host": "irctc1.p.rapidapi.com",
        },
      });
      if (response.data.status) {
        setTrains(response.data.data);
      } else {
        setTrains([]);
        alert("No trains found");
      }
    } catch (error) {
      console.error(error);
      alert("Failed to fetch train data");
    }
    setLoading(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!fromStationCode || !toStationCode || !dateOfJourney) {
      alert("Please fill all fields");
      return;
    }
    fetchTrains();
  };

  return (
    <>
      <Navbar />
      <div className={styles.container}>
        <div className={styles.searchCard}>
          <h1 className={styles.title}>Search Your Train</h1>
          
          <form onSubmit={handleSubmit} className={styles.searchForm}>
            <select 
              value={fromStationCode}
              onChange={(e) => setFromStationCode(e.target.value)}
              className={styles.select}
              required
            >
              <option value="">From Station</option>
              {stationList.map((station) => (
                <option key={station.code} value={station.code}>
                  {station.name} ({station.code})
                </option>
              ))}
            </select>

            <select 
              value={toStationCode}
              onChange={(e) => setToStationCode(e.target.value)}
              className={styles.select}
              required
            >
              <option value="">To Station</option>
              {stationList.map((station) => (
                <option key={station.code} value={station.code}>
                  {station.name} ({station.code})
                </option>
              ))}
            </select>

            <input
              type="date"
              value={dateOfJourney}
              onChange={(e) => setDateOfJourney(e.target.value)}
              className={styles.input}
              required
            />

            <button 
              type="submit"
              className={styles.button}
              disabled={loading}
            >
              {loading ? 'Searching...' : 'Find Trains'}
            </button>
          </form>

          {trains.length > 0 ? (
            <div className={styles.tableWrapper}>
              <table className={styles.table}>
                <thead>
                  <tr className={styles.tableHeader}>
                    <th className={styles.tableCell}>Train</th>
                    <th className={styles.tableCell}>Route</th>
                    <th className={styles.tableCell}>Departure</th>
                    <th className={styles.tableCell}>Arrival</th>
                    <th className={styles.tableCell}>Duration</th>
                    <th className={styles.tableCell}>Distance</th>
                  </tr>
                </thead>
                <tbody>
                  {trains.map((train) => (
                    <tr
                      key={train.train_number}
                      onClick={() => router.push(`/train-status?train=${train.train_number}`)}
                      className={styles.trainRow}
                    >
                      <td className={styles.tableCell}>
                        <span className={styles.trainNumber}>{train.train_number}</span>
                        <br />
                        {train.train_name}
                      </td>
                      <td className={styles.tableCell}>
                        {train.from_station_name} â†’ {train.to_station_name}
                      </td>
                      <td className={styles.tableCell}>{train.from_std}</td>
                      <td className={styles.tableCell}>{train.to_sta}</td>
                      <td className={styles.tableCell}>{train.duration}</td>
                      <td className={styles.tableCell}>{train.distance} km</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            !loading && (
              <div className={styles.noTrains}>
                Please search for available trains
              </div>
            )
          )}
        </div>
      </div>
    </>
  );
}
